a.link-active {
  font-weight: 400;
  color: rgb(78, 78, 255) !important;
  border-bottom: 1px solid rgb(78, 78, 255);
  /* padding-bottom: 0; */
}
.btn {
  /* outline: none !important; */
  box-shadow: none !important;
}
.navbar-toggler {
  box-shadow: none !important;
}
.navbar_toggler_icon div {
  width: 20px;
  margin: 3px;
  border: thin solid blue;
  height: 2px;
}
.nav-link {
  width: fit-content !important;
}
.navbar-brand {
  font-weight: 700;
  font-family: Arial, Helvetica, sans-serif;
  font-variant: small-caps;
  font-stretch: semi-condensed;
}
.nav-link:hover,
.navbar-brand:hover {
  color: rgb(78, 78, 255) !important;
}

.header {
  box-sizing: border-box;
  height: 83vh;
}
.animated {
  border-radius: 10px;
  animation: up-down 2s linear infinite alternate-reverse both;
}
.card {
  height: 100% !important;
  cursor: pointer;
  box-shadow: 0 0 6px 3px #ccc;
  transition: 0.5s all;
}
.card:hover{
  box-shadow: 0 0 1px 1px #ccc;
}
.card-body {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.service_header {
  padding: 1rem;
  position: sticky;
  background-color: white;
  top: 0;
  z-index: 1;
}
.lightDarkBtn{
  width: 55px;
    height: 55px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: black;
    border: 0;
    box-shadow: 3px 0px 5px #ccc !important;
    position: fixed;
    right: 20px;
    bottom: 20px;
    z-index: 1;
    cursor: pointer;
    color:white;
    font-weight: 900;
    transition:1s all;
}
.lightDarkBtn:hover{
  background-color: white;
  border:1px solid black;
  color:black;
}
@keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}
@media screen and (max-width: 360px) {
  .heading_title {
    font-size: 20px !important;
  }
  .header_desc {
    font-size: 14px !important;
  }
}
